.header {
	max-width: 880px;
	margin-top: 45px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 37px;
	border-bottom: 1px solid rgba(84, 84, 84, 0.7);
	background-color: inherit;
}
@media screen and (max-width: 730px) {
	.header {
		margin-top: max(28px, 8.75vw);
		margin-bottom: 42px;
	}
}
